
/* Styles for .Navi when menuHidden is true */
.Navi.menuHidden {
  width: 0;
  background-color: transparent;
}
.sideb-hidden {
  display: none;
}
/* Styles for .toggle when menuHidden is true */
.toggle.isOpen {
  margin-left: 250px;
}
.toggle {
  font-size: 24px;
  cursor: pointer;
  margin-left:0;
}
.toggle:hover {
  color: #ff9900;
}

.sideb {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  background-color: #000000;
  height:1000dvh;
}

.sideb h1::first-letter {
  color:#ff9900;

}
.sideb h1{
  color:#ffffff;
}
.sideb li{
  padding-bottom: 15px;
}
.sidebar li{  
    border-bottom: 1px solid #ff9900;

}
.sidebar a{
   color: #ffffff;
   text-decoration:none;
   font-size: 24px;
}
.sidebar a:hover{
   color: #ff9900;
   
}
.menubox {
 
  text-align: center; /* Center the text horizontally within the menubox */
  flex-grow: 1; /* Allow the menubox to take up available space */
}

/* Style for the menu items */
.menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
    display: flex;
  justify-content: center;
  align-items: center;
  
}

.menu li {
  display: inline; /* Display the list items horizontally */
  margin-right: 20px; /* Spacing between menu items */
}

/* Style for menu item links */
.menu li a {
  text-decoration: none;
  color: #000000; /* Text color for menu items */
  font-weight: bold;
  font-size: 18px;
}

/* Style for the currently active link */
.menu li a:hover {
  color: #ff9900; /* Change the color of the active link */
}


  .cross {
  position: absolute;
  top: 0;
  right: 0;
  padding: 10px;
  color: #fff; /* Adjust the color as needed */
  cursor: pointer;
  font-size: 30px;
}
.cross:hover{
  color:#ff9900;
}

.Navi {
 
  background-color: #ffffff; /* Background color for the Navi container */
  padding: 10px; /* Padding for the Navi container */
  z-index: 2;
  position: fixed;
  width: 100%; /* Make the navbar span the full width of the viewport */
  top:0;
}
